@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
:root {
  --color-primary: #00ad82;
  --color-primary--dark: #0a614b;
  --color-secondary: #625448;
  --color-grey--light: #e6e4e8;
  --color-grey--dark: #545454;
}

.x-header-landmark {
  display: none;
}

.page-header {
  margin-top: 30vh;
  margin-bottom: 10vh;
}

.church .hero {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #e6e4e8;
  padding: 20vh 0;
}
.church .hero--headline {
  color: #00ad82;
  margin: 0;
}
.church .hero--subheadline {
  color: black;
  font-size: 1.4em;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.church .candidates {
  padding: 65px 0;
}
.church .candidates .row {
  width: 80vw;
  margin: 3em auto;
  height: auto;
}
.church .candidates .intro--headline {
  color: black;
}
.church .candidates .intro--headline span {
  color: #00ad82;
}
.church .candidates .candidate {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;
  background-color: #e6e4e8;
  padding: 2em;
  border-radius: 1em;
}
.church .candidates .candidate__headshot {
  filter: grayscale(1) blur(2px);
  transition: all 0.2s ease;
  margin: 0 1em 0 0;
}
@media only screen and (max-width: 640px) {
  .church .candidates .candidate__headshot {
    margin: 0 0 1em 0;
  }
}
.church .candidates .candidate__headshot img {
  border-radius: 10px;
}
.church .candidates .candidate__display {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;
  max-width: 66%;
}
.church .candidates .candidate__display--name {
  margin: 0;
  color: black;
  font-size: 3em;
  letter-spacing: 0.06em;
}
.church .candidates .candidate__display--name span {
  color: #00ad82;
}
.church .candidates .candidate__display--summary {
  flex-shrink: 0;
  margin-bottom: auto;
}
.church .candidates .candidate__display--see-more {
  margin-top: 3em;
  display: block;
  font-family: "Bebas Neue", sans-serif;
  font-size: 1.4em;
  letter-spacing: 0.06em;
  color: #00ad82;
  border: 2px solid #00ad82;
  background-color: white;
  padding: 0.5em 0.8em;
  border-radius: 10px;
  justify-self: flex-end;
  transition: all 0.2s ease-out;
}
.church .candidates .candidate__display--see-more:hover {
  border: 2px solid transparent;
  background-color: #00ad82;
  color: white;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.35);
  transform: translateY(-5px);
}

.k1-author {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
}
.k1-author--photo {
  margin-right: 10px;
}
.k1-author--photo img {
  border-radius: 50%;
}
.k1-author__info-text {
  font-size: 1em;
}
.k1-author__info-text h2 {
  margin: 0;
}